html {
  scroll-behavior: smooth;
}
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: #18212b;
  color: white;
}
#root {
  height: 100%;
  width: 100%;
  @media (max-width: 1000px) {
    padding-top: 55px;
  }
}
// Header
.Header {
  height: 50px;
  width: 100%;

  border-bottom: 1px solid white;
  box-sizing: border-box;
}
.App {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.maid_div_page2 {
  height: 100%;
  width: 90%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;

  @media (max-width: 900px) {
    width: 100%;
  }
}
.main_div_navigation {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.link_price_block {
  display: flex;
  @media (max-width: 900px) {
    margin-right: 20px;
  }
}
.price_navigation_block {
  @media (max-width: 600px) {
    display: none;
  }
}
.Link {
  text-decoration: none;
  color: white;

  &:hover {
    color: white;
    text-shadow: 0px 0px 5px rgb(1, 255, 255);
    transition: all 0.4s linear;
  }
}
.Link_basket {
  margin-left: 20px;
  margin-right: 5px;
  width: 30px;
  height: 30px;
  background-image: url('./images/basket.svg');
}
.main_logo_div {
  margin-left: 20px;
  font-size: 25px;
  @media (max-width: 600px) {
    font-size: 19px;
  }
}
// Body
.main_div_StorePage {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 32% 32% 32%;
  column-gap: 2%;
  row-gap: 20px;
  margin-top: 10px;
  margin-bottom: 80px;

  @media (min-width: 1400px) {
    grid-template-columns: 24% 24% 24% 24%;
    column-gap: 1.33%;
  }
  @media (max-width: 800px) {
    grid-template-columns: 49% 49%;
  }
  @media (max-width: 520px) {
    grid-template-columns: 98%;
  }
}
// Footer
.Footer_div {
  padding-bottom: 22px;
  margin-top: 20px;
  height: 20px;
  font-size: small;
  width: 100%;
  border-top: 1px solid white;

  p {
    cursor: pointer;
  }

  @media (max-width: 900px) {
    padding-left: 30px;
  }
}

.g-signin2 {
  position: absolute;
  top: 5px;
  right: 10px;
}
.SignIn_div {
  height: 30px;
  width: 30px;
  margin-left: 10px;
  border-radius: 50%;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
